
  .intro {
    //background-image: url('~@/assets/Stars.png');
    min-height: 400px;
    padding-top: 120px;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .drank {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 110px;
  }
  .logo {
    position: relative;
    margin: 40px 0 60px 0;
    text-align: center;

    @media (min-width: 920px) {
      margin: 80px 0;
    }

    .nft {
      font-style: italic;
      font-size: 20px;
      position: absolute;
      right: 0;
      bottom: 0;
      color: #b63700;
    }
  }

  p {
    text-align: center;
  };

